<template>
    <div class="mine">
        <header>
            <div class="avatar">
                <img class="autoimg" src="../../assets/images/mine.jpg" alt="">
            </div>
            <div class="username one-txt">{{userInfo.userName}}</div>
            <div class="sign_out fr" @click="signOut">{{userInfo.loginTxt}}</div>
        </header>
        
        <div class="minebox">
            <nav>
                <ul class="clearfix">
                    <li>关注（0）</li>
                    <li @click="viewLikeMovie">收藏（{{curLikeMovieData.length}}）</li>
                    <li>会员</li>
                </ul>
            </nav>
            <div class="minelist">
                <div class="subnav">
                    <van-grid>
                        <van-grid-item icon="other-pay" text="我的消息" info="9"/>
                        <van-grid-item icon="idcard" text="电影票" />
                        <van-grid-item icon="coupon-o" text="优惠券" dot/>
                        <van-grid-item icon="pending-payment" text="我的钱包" />
                    </van-grid>
                </div>
                <div class="moreinfo">
                    <van-collapse v-model="activeNames">
                        <van-cell title="我的记录" is-link />
                        <van-cell title="我的影评" is-link />
                        <van-cell title="帮助与反馈" is-link />
                        <van-cell title="关于我们" is-link />
                        <van-collapse-item title="设置" name="1">
                            <div @click="signOut">退出当前用户</div>
                        </van-collapse-item>
                    </van-collapse>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Dialog } from 'vant';
    import {createNamespacedHelpers} from 'vuex'
    const {mapState} = createNamespacedHelpers('mineModule');
    export default {
        name:'mine',
        data() {
          return {
            activeNames: ['1']
          };
        },

        computed:{
            ...mapState(['userInfo','curLikeMovieData'])
        },

        created(){
            //获取用户登录状态信息
            let userStatus = localStorage.getItem('user');
            if(userStatus){
                let user = JSON.parse(userStatus); //存在则解析

                if(user.isLogin){
                    this.$store.commit('mineModule/changeUserInfo',
                    {loginTxt:'退出登录',userName:user.username});

                    // 获取所有用户收藏的电影数据
                    let likeMovieData = localStorage.getItem('likeMovieData');

                    likeMovieData = likeMovieData ? JSON.parse(likeMovieData) : {};

                    //获取当前用户收藏的电影数据
                    let curLikeMovieData = likeMovieData[user.username];
                    curLikeMovieData = curLikeMovieData ? curLikeMovieData : [];

                    //生成页面数据
                    this.$store.commit('mineModule/getCurLikeMovieData', curLikeMovieData);
                }
            }
        },
        
        methods:{
            //登出或登录
            signOut(){
               let userStatus = localStorage.getItem('user'); 
               if(userStatus){
                    let user = JSON.parse(userStatus); //存在则解析
                    
                    // 登录状态如果点击文本为退出登录则清空登录账号状态
                    if(user.isLogin){
                        Dialog.confirm({
                            title: '确定退出登录吗？',
                            message:'期待主人重新归来！'
                            }).then(() => {

                            localStorage.removeItem('user'); //清空登录状态
                            this.$store.commit('mineModule/changeUserInfo',{loginTxt:'登录',userName:''});
                        }).catch(() => {
                          // on cancel
                        });

                    }else{
                        return this.$router.push({name:'login'});
                    }
                }else{
                    return this.$router.push({name:'login'});
                }
            },

            viewLikeMovie(){
                //验证是否登录
                let userStatus = localStorage.getItem('user');
                if (userStatus) {
                    let userInfo = JSON.parse(userStatus);
                    //如果登录
                    if (userInfo.isLogin) {
                        // console.log('查看我的收藏电影');
                        this.$router.push({name: 'likeMovie'});//跳转
                    } else {
                        //如果未登录
                        this.$toast('请先登录');
                    }
                }else{
                    this.$toast('您还没有登录');
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .mine{
        header{
            height: 110px;
            position: relative;
            background-color: #697A9C;
            color:#fff;
            .avatar{
                width: 70px;
                height: 70px;
                background-color: #f5f5f5;
                position: absolute;
                bottom:-20px;
                left: 30px;
                border-radius: 50%;
                box-shadow: 1px 1px 1px rgb(253, 76, 106);
                >img{
                    border-radius: 50%;
                }
            }
            .username{
                width: 100px;
                height: 20px;
                line-height: 20px;
                // background-color: #ddd;
                position: absolute;
                bottom: 8px;
                left: 114px;
                font-size: 20px;
                overflow: hidden;
            }
            .sign_out{
                font-size: 14px;
                margin: 20px 20px;
            }
            
        }
        
        .minebox{
            margin-top: 20px;
            nav{
                padding-top: 10px;
                ul li{
                    float: left;
                    width: 33.334%;
                    text-align:center;
                    font-size: 14px;
                }
            }
            .minelist{
                width: 90%;
                margin: 14px auto;
                .subnav{
                    border-radius: 14px;
                    border: 1px solid #ddd;
                }
                .moreinfo{
                    margin-top: 20px;
                }
            }
        }

    }
</style>